<template>
  <div ref="outlinebtn"
    class="outlinebtn"
    :style="{width:width+'px',height:height+'px',color:hostinfo[7],borderColor:hostinfo[7]}"
  >{{btnname}}</div>
</template>

<script>
export default {
  props: ["width", "height", "btnname"],
  data() {
    return {
      maincolor: "#9b1e32"
    };
  },
  computed:{
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
  },
  mounted(){
      let maincolor = this.maincolor
    this.$refs.outlinebtn.addEventListener("mouseover", function() {
      this.style.backgroundColor = maincolor;
      this.style.color = 'white';

    });
    this.$refs.outlinebtn.addEventListener("mouseleave", function() {
       this.style.backgroundColor = 'white';
      this.style.color = maincolor;
    });
  }
};
</script>

<style lang="scss" scoped>
.outlinebtn {
  margin-top: 25px;
  border: solid 1px;
  border-radius: 10px;
  padding-top: 13px;
  padding-bottom: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  transition: 0.3s;
}

</style>