<template>
  <div class="searchbarround">
    <input ref="searchbarround" @keyup.enter="keywordhandler" type="text" placeholder=".." v-model="searchkeyword" />
    <i class="ri-search-2-line" @click="keywordhandler"></i>
  </div>
</template>

<script>
export default {
  data() {
    return {
      maincolor: "#9b1e32",
      searchkeyword:''
    };
  },
  mounted() {
    let maincolor = this.hostinfo[7];
    this.$refs.searchbarround.addEventListener("focus", function() {
      this.style.border = "solid 1px " + maincolor;
    });
    this.$refs.searchbarround.addEventListener("blur", function() {
      this.style.border = "solid 1px #4f4f4f";
    });
  },
  methods:{
      keywordhandler(){
          this.$emit('roundemitkeyword',this.searchkeyword)
      }
  },computed:{
       hostinfo() {
      return this.$store.state.hostinfo[0];
    },
  }
};
</script>


