<template>
  <div>
    <div class="session">
      <!-- <div class="title">
            <div></div>
            
      </div>-->
      <div class="availableinfo">
        <span class="left">
          
            <span class="num1" v-if="availablenum">{{ signupinfolist[7] - this.usereventinfo.length }}</span>
          <span class="num1" v-else></span>
        
        </span>
        <span>
          
          <span class="num1">5</span>
        </span>
      </div>
      <!-- <div class="ticketwrap">
            <Ticket></Ticket>
      </div>-->
    </div>
    <div class="btnwrap">
      <fillbtn
        btnname=""
        width="329"
        height="45"
        @clickhandler="tosignupinfo"
      ></fillbtn>
    </div>
    <alertblock v-show="showalert"></alertblock>
     <div class="loadingmask" v-show="loadingcircle">
        <div class="loader"></div>
    </div>
  </div>
</template>

<script>
import fillbtn from "../components/fillbtn";
// import Ticket from '../components/ticket'
import axios from "axios";
import alertblock from "../components/Alertblock";
export default {
  data() {
    return {
      // availablenum: null,
      waitingnum: 0,
      emitarr: [],
      hassignup: false,
      loadingcircle: true,
      usereventinfo:[]
    };
  },
  created() {
    //mouted
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbxqMsajmkgXY6oxaJlCh3tRP5Rr8MEh0GpBSkcR7amFy7cL_YorIGZvftoQnSv6Nn7R8Q/exec",
      params: {
        mode: "user",
        //ididget
        id: this.$route.params.id,
      },
    })
      .then((response) => {

        this.usereventinfo =  response.data;
        this.loadingcircle = false
        this.emitarr[0] = this.userdata.displayName;
       
        //
        for (let i = 0; i < response.data.length; i++) {
          if (response.data[i][0] == this.userdata.displayName) {
            this.hassignup = true;
          }
        }
      })
      .catch((error) => {
        console.error(error);
      });
  },
  mounted() {},
  methods: {
    tosignupinfo() {
      //
      if (this.hassignup == true) {
        this.$store.dispatch("showalertani", "show");
        setTimeout(() => {
          this.$store.dispatch("showalertani", "close");
        }, 2000);
      } else {
        //commitmvp
        // this.$store.commit("tosuccesspage");

        // this.$store.commit("setpostsignupinfo", this.emitarr);
        // this.$store.dispatch("postmemberinfo");

        this.$store.commit('addsignupnumber')
      }
    },
  },
  computed: {
    signupinfolist() {
      return this.$store.state.signupinfo;
    },
    showalert() {
      return this.$store.state.alertshow;
    },
    userdata() {
      return this.$store.state.userprofiledata;
    },
    availablenum(){
      let availablenum = 0
      availablenum =  this.signupinfolist[7] - this.usereventinfo.length;
      return availablenum
    }
  },
  components: {
    fillbtn,
    // Ticket,
    alertblock,
  },
};
</script>

